@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
/* SCSS HEX */
$eggshell: #faf3ddff;
$laurel-green: #c8d5b9ff;
$cambridge-blue: #8fc0a9ff;
$cambridge-blue-dark: rgb(109, 165, 139);
$green-sheen: #68b0abff;
$green-sheen-dark: #599692;
$amazon: #649773;
$amazon-dark: #4a855d;

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  font-family: 'Roboto', sans-serif;
  text-align: center;
  color: rgba(0, 0, 0, 0.726);
}
.tabs {
  display: none;
}
.app {
  min-height: 100vh;
  display: flex;
}

.category {
  width: 33.33%;
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: $eggshell;
    padding: 0.7em 1em;
  }
  
  .number {
    font-size: 1.4em;
    font-weight: 600;
  }
}
.category:first-of-type {
  .header {
    background-color: $cambridge-blue-dark;
  }
  background-color: $cambridge-blue;
}
.category:nth-of-type(2) {
  .header {
    background-color: $green-sheen-dark;
  }
  background-color: $green-sheen;
}
.category:nth-of-type(3) {
  .header {
    background-color: $amazon-dark;
  }
  background-color: $amazon;
}

.add-form {
  text-align: left;
  margin: 1em 2em;
  div {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  input {
    font-size: 16px;
    background-color: transparent;
    width: 100%;
    margin-bottom: 0.7em;
    margin-left: 1em;
    padding: 0.5em 0.3em;
    border: none;
    border-bottom: 1px solid rgba(0, 0, 0, 0.219);
    outline: none;
  }
  input:focus {
    border-bottom-color: rgba(0, 0, 0, 0.411);
  }
  ::-webkit-input-placeholder{
    color:rgba(0, 0, 0, 0.61);
  }
}

.task {
  background-color: $eggshell;
  padding: 1em 1.5em;
  margin: 1em;
  border-radius: 10px;
  text-align: left;
  font-weight: 600;
  h3,
  p {
    margin-bottom: 0.5em;
  }
  h3 {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .details {
    opacity: 0.6;
    line-height: 1.3;
  }
  .time {
    display: flex;
    align-items: flex-start;
    color: orange;
    font-size: 0.9em;
    margin-bottom: 0;
    margin-top: 1.5em;
    font-weight: 600;
    p {
      margin-left: 0.3em;
    }
  }
  .down-icon {
    display: inline;
    text-align: center;
    color: initial;
    background-color: transparent;
    cursor: pointer;
  }
  .down-icon:hover {
    opacity: 0.8;
  }

  .task-footer {
    margin-top: 0.5em;
    display: flex;
    justify-content: space-between;
  }
}

//buttons 
button {
  letter-spacing: 1px;
  color: $eggshell;
  padding: 0.5em 0.8em;
  font-size: 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-weight: 600;
  display: block;
}
button:hover {
  opacity: 0.8;
}
button[type="submit"] {
  margin-top: 1em;
}
.delete-btn {
  background-color: rgb(224, 62, 62);
}
.finished-btn {
  background-color: $amazon-dark;
}
.stopped-btn {
  background-color: $cambridge-blue-dark;
}
.select-btn {
  background-color: $green-sheen-dark;
}
.round {
  color: rgba(0, 0, 0, 0.753);
  display: inline-block;
  background-color: $eggshell;
  padding: 0;
  margin: 0.5em auto;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  font-size: 1.7em;
  font-weight: 600;
}
//end of buttons

//mobile 
@media screen and (max-width: 700px) {
  .app {
    display: none;
  }
  .tabs {
    display: block;
  }
  .category {
    width: 100%;
    min-height: 100vh;
    padding: 0.5em;
  }
}
.tab{
  padding: 1em 7px;
  text-align: center;
  font-weight: 600;
  color:white;
  .tab-header{
    align-items: center;
    display: flex;
    justify-content: space-between;
  }
}
.tab:first-child {
  background-color: $cambridge-blue;
  width: 32%;
}
.tab:nth-child(2) {
  background-color: $green-sheen;
  width: 34%;
}
.tab:nth-child(3) {
  background-color: rgb(100, 151, 115);
  width: 34%;
}
.center {
  display: flex;
  justify-content: center;
  align-items: center;
}
.icon{
  margin-right: .2em;
}
//end of mobile